input {
    border: none;
    outline: none;
    font-size: 22px;
    margin-bottom: 24px;
    padding: 8px;
    border-radius: 6px;
}

h4 {
    color: white;
    font-size: 32px;
    margin-top: -20px;
}

.container {
    width: 100%;
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 450px;
    height: 400px; 
    border-radius: 25px;
    background-color: #ff4b33;
  }


button {
    font-size: 24px;
    border: none;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 12px;
    font-weight: bold;
    color: #ff4b33;
    background-color: white;
    width: 160px;
}

button:hover {
    background-color: rgb(230,230,230);
    /* color: rgb(200,58.8,40); */
}